<template>
<div class="Partner-contract">
  <Row type="flex" justify="start" class="code-row-bg contract-tab">
      <Col :md="18" :lg="20">
        <div class="clearfix">
          <div  class="contract-btn left"  @click="wait" :class="{'active': active == 1}">{{$t("common.Pending_contract")}}</div>
          <div  class="contract-btn left" @click="past" :class="{'active': active == 2}">{{$t("common.Audit_pass_contract")}}</div>
          <div  class="contract-btn left" @click="reject" :class="{'active': active == 3}">{{$t("common.Rejected_contract")}}</div>
        </div>
        
      </Col>
      
      
      <Col span="4">
        <div class="contract-btn Add_contract"  @click="Add_contract">{{$t("common.Add_contract")}}</div>
      </Col>
  </Row>
  <hr>
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Contract_No")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Contract_name")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="9">
        <p>{{$t("common.Cooperative_company_name")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="space-around" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'Partner_contract',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            columns1: [
                {
                    title: this.$t('common.Contract_No'),
                    key: 'contract_number',
                    align:'center'
                },
                {
                    title: this.$t('common.Cooperative_company'),
                    key: 'contract_name',
                    align:'center'
                },
                {
                    title: this.$t('common.Contract_name'),
                    key: 'contract_type',
                    align:'center'
                },
                {
                    title: this.$t('common.Submission_time'),
                    key: 'time',
                    align:'center'
                },
                {
                    title: this.$t('common.Operate'),
                    key: 'operation',
                    align:'center',
                    render: (h, params) => {
                      return h('div', [
                          h('Button', {
                              props: {
                                  type: 'primary',
                                  size: 'small'
                              },
                              style: {
                                  marginRight: '10px'
                              },
                              on: {
                                  click: () => {
                                      this.$router.push({
                                        path: '/Contract_details',
                                        name: 'Contract_details',
                                        params: {
                                            name: 'editorContent',
                                            send_data: this.editorContent
                                        }
                                      });
                                  }
                              }
                          }, this.past_btn),
                          h('Button', {
                              props: {
                                  type: 'error',
                                  size: 'small'
                              },
                              style:{
                                display:this.show
                              },
                              on: {
                                  click: () => {
                                      this.remove(params.index)
                                  }
                              }
                          }, this.reject_btn)
                      ]);
                  }
                }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null,
            past_btn:this.$t("common.Adopt"),
            reject_btn:this.$t("common.Overrule"),
            active:1,
            show:'inline',
            
        }
    },
    methods:{
      wait () {
        this.past_btn = this.$t("common.Adopt")
        this.reject_btn = this.$t("common.Overrule")
        this.show = 'inline'
        this.active = 1
      },
      past () {
        this.past_btn = this.$t("common.Overrule")
        this.show = 'none'
        this.active = 2
      },
      reject () {
        this.past_btn = this.$t("common.Adopt")
        this.show = 'none'
        this.active = 3
      },
      Add_contract (){
        this.$router.push('/Add_contract');
      },
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              data.push({
                  contract_number: 'DG2018031200'+i,
                  contract_name: parseInt(Math.random()*(3-1)+0)>0?'深圳怡亚通股份有限公司':'中国能源建设集团有限公司',
                  contract_type: parseInt(Math.random()*(3-1)+0)>0?'战略合作协议':'技术入股协议',
                  time:this.formatDate(this.getRandomDateBetween()),
                  operation:''
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      onedata (data,index){
        //console.log(data,index)
        this.sendParams ()
      },
      sendParams () {
          this.$router.push({
              path: '/Contract_details',
              name: 'Contract_details',
              params: {
                  name: 'contract_state',
                  send_data: this.active
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       },
    },
    created(){
         this.handleListApproveHistory();
    }
}
</script>

<style lang="css">
.Partner-contract{
  
}
.Partner-contract .search_criteria{
  text-align: center;
  line-height: 50px;
  background: #fff;
  margin-top: 10px;
}
.Partner-contract .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  text-align: center;
  background: #fff;
}
.Partner-contract .contract-btn{
  width: 200px;
  height: 40px;
  border: 1px solid #ccc;
  text-align: center;
  line-height: 40px;
  font-size: 16px;
  cursor: pointer;
  background-color: white;
  font-weight: bold;
}
.Partner-contract .contract-tab{
  margin-bottom: 20px;
  text-align: right;
}
.Partner-contract .active{
  background-color: #409EFF;
  color: #fff;
}
.Partner-contract .search_criteria p{
  height: 30px;
}
.Partner-contract .Add_contract{
  background-color: #2d8cf0;
  color: white;
  border-radius: 4px;
}
</style>



